'use strict'
/**
 * 
 * https://github.com/jaywcjlove/validator.js
 *
 *  required, mobile, email, tel, url, min, max, min_length, max_length, integer, confirm
 *
 * 
 *
 * import Validator from '../utils/validator'
 *
 * let v = new Validator
 * v.validate({
 *    value: 'XXXXXX'
 *    rules: 'required|min_length(6)|max_length(20)',
 *    errors: '|6|20',
 * })
 * let error = v.error
 * let faild = v.faild
 * let passed = v.passed
 *
 */
const regexs = {
  //  max_length(12) => ["max_length",12]
  rule: /^(.+?)\((.+)\)$/,
  // 
  numericRegex: /^[0-9]+$/,
  /**
   * @descrition:
   * 1.a-zA-Z0-91.
   * 2.-_.
   * 3.@
   * 4.26
   * 5.-_.
   */
  email: /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/,
  /**
   * [ip ipv4ipv6]
   * "192.168.0.0"
   * "192.168.2.3.1.1"
   * "235.168.2.1"
   * "192.168.254.10"
   * "192.168.254.10.1.1"
   */
  ip: /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])((\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){3}|(\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){5})$/,
  /**
   * @descrition:
   * 
   * (23)-(23)-(78)-(3)
   **/
  fax: /^(([0\+]\d{2,3}-)?(0\d{2,3})-)(\d{7,8})(-(\d{3,}))?$/,
  /**
   *@descrition:
   * 13130131132133134135136137138139
   * 14145147
   * 15150151152153155156157158159
   * 17170176177178
   * 18180181182183184185186187188189
   *  (+86)
   */
  mobile: /^((\+?[0-9]{1,4})|(\(\+86\)))?(13[0-9]|14[57]|15[012356789]|17[0678]|18[0-9])\d{8}$/,
  /**
   * @descrition  URL
   */
  url: /[a-zA-z]+:\/\/[^\s]/
}

/**
 * [backVal  field  ]
 * @param  {[type]}              [Object or String]
 * @return {[type]}              [String]
 */
function backVal(field) {
  return (typeof field === 'string') ? field : field.value
}

const _testHook = {
  // 
  email: function(field) {
    return regexs.email.test(backVal(field));
  },
  //  ip 
  ip: function(field) {
    return regexs.ip.test(backVal(field));
  },
  // 
  fax: function(field) {
    return regexs.fax.test(backVal(field));
  },
  // 
  tel: function(field) {
    return regexs.fax.test(backVal(field));
  },
  // 
  mobile: function(field) {
    return regexs.mobile.test(backVal(field));
  },
  // URL
  url: function(field) {
    return regexs.url.test(backVal(field));
  },
  // 
  required: function(field) {
    let value = backVal(field);
    return (value !== null && value !== '')
  },
  // 
  max_length: function(field, length) {
    if (!regexs.numericRegex.test(length)) return false
    return (backVal(field).length <= parseInt(length, 10))
  },
  // 
  min_length: function(field, length) {
    if (!regexs.numericRegex.test(length)) return false
    return (backVal(field).length >= parseInt(length, 10))
  },
  // 
  min(field, min) {
    return (backVal(field) >= parseInt(min, 10))
  },
  // 
  max(field, max) {
    return (backVal(field) <= parseInt(max, 10))
  },
  // 
  integer(field) {
    return /^(-?[1-9]\d*|0)$/.test(backVal(field))
  },
  // 
  //  password.value  watch , 
  // watch: {
  //   "password.value": function(val) {
  //     this.repassword.rules = `required|min_length(6)|max_length(20)|confirm(${val})`
  //   },
  // },
  confirm(field, confirm) {
    return (backVal(field) === confirm)
  }
}

class Validator {

  constructor() {
    this.passed = true
    this.faild = false
    this.error = ''
  }

  validate(field) {

    let rules = field.rules.split('|'),
      isEmpty = (!field.value || field.value === '' || field.value === undefined)

    for (let i = 0, ruleLength = rules.length; i < ruleLength; i++) {
      let method = rules[i];
      let parts = regexs.rule.exec(method);

      let param = null

      //  max_length(12)
      if (parts) method = parts[1], param = parts[2]

      if (typeof _testHook[method] === 'function') {
        if (!_testHook[method].apply(this, [field, param])) {
          this.faild = true
        }
      }
      if (regexs[method] && /^regexp\_/.test(method)) {
        if (!regexs[method].test(field.value)) {
          this.faild = true
        }
      }
      if (this.faild) {
        this.error = (function() {
          return field.errors.split('|')[i]
        })()
        break;
      }
    }

    this.passed = !this.faild

    return this
  }
}

export default Validator



